.button {
    padding: 17px 22px;
    font-weight: 600;
    color: var(--neutral-white);
    background-color: var(--primary-moderate-blue);
    border-radius: 10px;
}
.button:hover{
    background-color: var(--primary-light-grayish-blue);
}

@media (max-width: 767px) {
    .button {
            padding:10px 20px;
            font-weight: 600;
            font-size: 11px;
            color: var(--neutral-white);
            background-color: var(--primary-moderate-blue);
            border-radius: 8px;
        }
    
        .button:hover {
            background-color: var(--primary-light-grayish-blue);
        }
}