.score {
    width: 40px;
    height:100px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: var(--primary-moderate-blue);
    text-align: center;
    font-weight: bold;
    background-color: var(--neutral-very-light-gray);
    border-radius: 4px;

}
.score> *{
    flex: 1;
}
.score button {
    color: var(--primary-moderate-blue);
background: transparent;
}

@media (max-width: 767px) {
    .score {
            width: 100px;
            height: 40px;
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 10px;
            color: var(--primary-moderate-blue);
            text-align: center;
            font-weight: bold;
            background-color: var(--neutral-very-light-gray);
            border-radius: 4px;
    
        }
        
}