.comment {
    display: grid;
    grid-template-columns: 40px 1fr 1fr 1fr;
    grid-template-rows: 48px auto auto;
    grid-template-areas:
        "score header header reply"
        "score text text text "
        "..... .... .... update "
    ;
    padding: 10px 20px;
    margin-bottom: 5px;
    background-color: var(--neutral-white);
    border-radius: 10px;
}

.mt {
    margin-top: 20px;
}

.comment .comment-score {
    grid-area: score;
    margin-top: 10px;
}

.comment .comment-header {
    grid-area: header;
    display: flex;
    gap: 10px;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 18px;
}

.comment .comment-reply-icon {
    grid-area: reply;
    display: flex;
    justify-content: flex-end;
}

.comment .comment-content {
    grid-area: text;    
    width: 100%;
    padding-left: 16px;
    border: none;
    border-radius: 10px;
    user-select: none;
    resize: none;
    background-color: transparent;
    outline: none;
}

.comment .comment-footer {
    grid-area: update;
    align-self: center;
    text-align: right;
    margin-top: 6px;
}

.comment-header .you {
    display: inline-block;
    padding: 1px 6px;
    margin: 0 5px;
    color: var(--neutral-white);
    background-color: var(--primary-moderate-blue);
}

.comment-content.outline {
    border: 1px solid #EAEBEF;
}

.comment-content.outline:active,
.comment-content.outline:focus {
    border: 1px solid transparent;
    outline: 1px solid var(--primary-moderate-blue);

}



@media (max-width: 767px) {
    .comment {
        display: grid;
        grid-template-columns: 1fr 1fr auto;
        grid-template-rows: 24px 1fr auto auto;
        row-gap: 0px;
        grid-template-areas:
            "header header header"
            "body body body"
            "footer-left footer-center footer-right"
            "button button button";
        padding: 12px;
        margin: 10px;
        background-color: var(--neutral-white);
        border-radius: 10px;
    }

    .mt {
        margin-top: 20px;
    }

    .comment .comment-score {
        grid-area: footer-left;
    }

    .comment .comment-header {
        grid-area: header;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    .comment .comment-header p {
        margin: 0;
    }

    .comment .comment-reply-icon {
        grid-area: footer-right;
        display: flex;
        justify-content: flex-end;

    }

    .comment .comment-content {
        grid-area: body;
        padding: 12px 1px;
    }

    .comment .comment-footer {
        grid-area: button;
        margin-top: 0px;
        text-align: right;
    }

    .comment-header .you {
        display: inline-block;
        padding: 1px 6px;
        margin: 0 5px;
        color: var(--neutral-white);
        background-color: var(--primary-moderate-blue);
    }

    .comment-header div:last-child {
        margin: 0;
    }


}