.delete-dialog-box-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 1;
}

.delete-dialog-box {
    width: 256px;
    min-height: 160px;
    padding: 20px;
    border-radius: 10px;
    background-color: var(--neutral-white);

}

.delete-dialog-box h3 {
    margin: 0;
}

.delete-dialog-box p {
    color: var(--neutral-grayish-blue);
    font-size: 15px;
    margin: 8px 0 16px;
}

.button-group {
    display: flex;
    justify-content: space-between;
    gap: 12px;
}

.button-group button {
    flex: 1;
    font-size: 10px;
    font-weight: 600;
    color: var(--neutral-white);
    background-color: var(--neutral-grayish-blue);
    padding: 8px 8px;
    border-radius: 4px;
}

.button-group button:last-child {

    background-color: var(--primary-soft-red);
}