.textarea {
    min-height: 67px;
    width: 100%;
    padding: 10px;
    overflow: auto;
    border: none;
    border-radius: 10px;
    user-select: none;
    resize: none;
    background-color: transparent;
    outline: none;
}

.outline {
    border:1px solid #EAEBEF;
}
.outline:active,
.outline:focus
 {
    border: 1px solid transparent;
    outline: 1px solid var(--primary-moderate-blue);
    
}
@media (max-width: 767px) {
    .textarea {
            min-height: 90px;
            width: 100%;
            padding:10px;
            
        }
}