.comment-reply {
    display: flex;
    gap: 20px;
    padding: 20px;
    margin-bottom: 14px;
    background-color: var(--neutral-white);
    border-radius: 10px;
}

.comment-reply>div:nth-child(2) {
    flex: 1;
}

@media (max-width: 767px) {
    .comment-reply {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto 35px;
        grid-template-areas:"text text" "avatar button";
        gap: 5px;
        padding: 10px 0px;
        margin: 0 10px;
        margin-bottom: 25px;
        background-color: var(--neutral-white);
    }

    .comment-reply>div:nth-child(2) {
        flex: none;
    }
        .comment-reply .comment-reply-avatar {
            grid-area: avatar;
            padding: 0 10px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }
        .comment-reply .comment-reply-text {
            grid-area: text;
            padding: 0 10px;
        }
        .comment-reply .comment-reply-button {
            grid-area: button;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            padding: 0 10px;
        }
}