.edit-button button {
    padding: 20px;
    font-weight: 600;
}

.edit-button span {
    margin-left: 5px;
}

.edit-button button:first-child {

    color: var(--primary-soft-red);
}

.edit-button button:first-child:hover {
    color: var(--primary-pale-red);
}

.edit-button button:last-child {
    color: var(--primary-moderate-blue);
}

.edit-button button:last-child:hover {
    color: var(--primary-light-grayish-blue);
}

@media (max-width: 767px) {
    .edit-button {
        display: flex;
        gap: 10px;
    }

    .edit-button button {
        padding: 0px;
        font-weight: 600;
    }


}